	module.exports = {"data":{"markdownRemark":{"html":"<p>If you elect to <strong>Start H1 Response</strong>, you'll be taken to the Setup Guide where you can walk through the product on how to set up your vulnerability disclosure policy and successfully launch a program. </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/getting-started-2-9d26db082162185b71015dfdf5cf2cc5-c485f.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 81.91214470284238%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"getting-started-2\"\n        title=\"\"\n        src=\"/static/getting-started-2-9d26db082162185b71015dfdf5cf2cc5-e7ffa.png\"\n        srcset=\"/static/getting-started-2-9d26db082162185b71015dfdf5cf2cc5-49813.png 125w,\n/static/getting-started-2-9d26db082162185b71015dfdf5cf2cc5-dc8a5.png 250w,\n/static/getting-started-2-9d26db082162185b71015dfdf5cf2cc5-e7ffa.png 500w,\n/static/getting-started-2-9d26db082162185b71015dfdf5cf2cc5-dda24.png 750w,\n/static/getting-started-2-9d26db082162185b71015dfdf5cf2cc5-c5f18.png 1000w,\n/static/getting-started-2-9d26db082162185b71015dfdf5cf2cc5-22484.png 1500w,\n/static/getting-started-2-9d26db082162185b71015dfdf5cf2cc5-c485f.png 1548w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol>\n<li>Select <strong>Edit</strong> to edit your policy and scope. </li>\n<li>Select <strong>Edit</strong> to edit your profile. </li>\n<li><em>(Optional)</em> Click the <strong><a href=\"human-augmented-signal.html\">Human Augmented Signal</a></strong> toggle to be either on or off. </li>\n<li>Once you've completed all of the fields in step 1 of the Setup Guide, click <strong>Submit for Approval</strong> to have HackerOne review your program. </li>\n<li>\n<p>If your program is approved by HackerOne, your program will be placed in controlled launch where it'll remain private and visible to only a select number of hackers. To publicly launch your program, your program must:</p>\n<ul>\n<li>Receive at least 10 reports and have invited 100 hackers</li>\n<li>Meet the baseline responsiveness limits</li>\n</ul>\n</li>\n<li>Once you've met the criteria, the <strong>Public Launch</strong> button will appear. You can publicly self-launch your program when you're ready to. </li>\n</ol>","frontmatter":{"path":"/programs/start-h1-response.html","id":"programs/start-h1-response","title":"Start H1 Response","description":null}}},"pathContext":{}}

